import { AddElementsCommand } from "../commands"
import { Publisher, PublisherEvent } from "../shared/ObserverPattern"
import { centerElementsOnCenterOfPosition, Position } from "../utils/WallElementsUtils"
import { Application } from "./Application"
import { SerializedElement, Serializer } from "./Serializer"
import { Wall } from "./Wall"

export class ClipboardElementsChanged extends PublisherEvent {}
export class CutEvent extends PublisherEvent {}
export class CopyEvent extends PublisherEvent {}

export type ClipboardEventsType =
	| ClipboardElementsChanged
	| CutEvent
	| CopyEvent

export const ClipboardEvents : ClipboardEventsType[] = [ClipboardElementsChanged, CutEvent, CopyEvent]

export class Clipboard extends Publisher {
	private elements : SerializedElement[] = []

	constructor (private serializer: Serializer) {
		super()
	}

	getElements () {
		return this.elements
	}

	setElements (elements: SerializedElement[]) {
		this.elements = elements
		this.notify(new ClipboardElementsChanged())
	}

	cutSelectedElements (app: Application, wall: Wall) {
		const selectedElements = wall.selectedElements
		this.elements = this.serializer.serialize(selectedElements)
		app.removeSelected()
		this.notify(new ClipboardElementsChanged())
		this.notify(new CutEvent())
	}

	copySelectedElements (wall: Wall) {
		const selectedElements = wall.selectedElements
		this.elements = this.serializer.serialize(selectedElements)
		this.notify(new ClipboardElementsChanged())
		this.notify(new CopyEvent())
	}

	paste (app: Application, wall: Wall) {
		const elements = this.serializer
			.deserialize(this.elements)
			.map(element => element.duplicate())

		const command = new AddElementsCommand(elements)
		app.executeCommand(command)

		wall.selectElements(elements)
	}

	isPasteAvailable () {
		return this.elements.length > 0
	}

	pasteOnPosition(app: Application, wall: Wall, position: Position) {
		const elements = this.serializer
			.deserialize(this.elements)
			.map(element => element.duplicate())

		centerElementsOnCenterOfPosition(elements, position)

		const command = new AddElementsCommand(elements)
		app.executeCommand(command)

		wall.selectElements(elements)
	}
}
