import { css, html } from 'lit'
import {customElement} from 'lit/decorators.js'
import { BulkModeModal } from '../Modal/BulkModeModal'
import { IconName, icons } from './icons'
import { notesColors } from '../notesColors'
import { ToolbarItemWithChildrens } from './ToolbarItemWithChildrens'
import { Note, WallElementId } from '@eswall/core'

@customElement('ui-new-note-toolbar-item')
export class NewNoteToolbarItem extends ToolbarItemWithChildrens {
	static styles = [
		...super.styles,
		css`
			.color-item {
				height: var(--element-size-unit);
				width: var(--element-size-unit);
				border-radius: 50%;
				cursor: pointer;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
			}

			svg {
				height: calc(var(--spacing-unit) * 3);
				width: calc(var(--spacing-unit) * 3);
			}

			.color-item:hover {
				background: var(--background-secondary);
			}
			.color {
				height: calc(var(--spacing-unit) * 3);
				width: calc(var(--spacing-unit) * 3);
				background: red;
				border-radius: 50%;
				border: 2px solid #2a2b2b;
				cursor: pointer;
			}
			.color-item:hover .color {
				border-color: #025E73;
			}
		`
	]
	icon : IconName = 'note'

	constructor () {
		super()
	}

	// eslint-disable-next-line @typescript-eslint/no-empty-function
	clickHandler(): void {}

	subitems () {
		return html`
			${notesColors.map(color => html`
				<div
					class="color-item"
					@click="${this.colorClickHandler(color)}"
				>
					<div
						class="color"
						style="background: ${color};"
					></div>
				</div>
			`)}
			<div
				class="color-item"
				@click="${this.activeBulkMode}"
			>
				${icons.many()}
			</div>
		`
	}

	private colorClickHandler (color: string) {
		return () => {
			this.closeSubitems()
			const id = WallElementId.generate()
			const note = new Note(id, {
				content: '',
				background: color,
				position: {
					x: 0, y: 0
				}
			})
			this.app.createWallElementOnCanvasClickedPoint(note)
		}
	}

	private activeBulkMode () {
		BulkModeModal.open(this.app)
	}
}
