import { canvasBuilder } from '@eswall/canvas'
import { Application, CommandHistory, Wall } from '@eswall/core'
import {LitElement, css, html} from 'lit'
import {customElement, query, state} from 'lit/decorators.js'
import '@eswall/ui'
import { PlatformAdapter } from '@eswall/adapters-browser'
import { shortcutManagerBuilder } from '@eswall/shortcuts'
import { Clipboard } from '@eswall/core/src/core/Clipboard'

@customElement('eswall-container')
export class ESWallContainer extends LitElement {
	static styles = css`
		#container {
			position: relative;
			width: 100vw;
			height: 100vh;
			overflow: hidden;
		}
		ui-toolbar.left {
			position: fixed;
			top: 50%;
			left: 20px;
			transform: translateY(-50%);
		}
		ui-toolbar.top {
			position: fixed;
			left: 50%;
			top: 20px;
			transform: translateX(-50%);
		}
	`

	@query('#container') container !: HTMLDivElement
	@state() app !: Application

	firstUpdated () {
		this.app = new Application(
			new Wall([]),
			new CommandHistory(),
			new PlatformAdapter(),
			new Clipboard(),
			canvasBuilder(
				this.container,
				shortcutManagerBuilder()
			)
		)
	}

	render() {
		const toolbars = html`
			<ui-toolbar class="left" layout="vertical">
				<ui-new-note-toolbar-item .app="${this.app}"></ui-new-note-toolbar-item>
				<ui-new-text-toolbar-item .app="${this.app}"></ui-new-text-toolbar-item>
				<div class="divider"></div>
				<ui-background-toolbar-item .app="${this.app}"></ui-background-toolbar-item>
			</ui-toolbar>
			<ui-toolbar class="top">
				<ui-logo></ui-logo>
				<div class="divider"></div>
				<ui-import-toolbar-item .app="${this.app}"></ui-import-toolbar-item>
				<ui-save-file-toolbar-item .app="${this.app}"></ui-save-file-toolbar-item>
				<ui-undo-toolbar-item .app="${this.app}"></ui-undo-toolbar-item>
				<ui-redo-toolbar-item .app="${this.app}"></ui-redo-toolbar-item>
				<div class="divider"></div>
				<ui-copy-toolbar-item .app="${this.app}"></ui-copy-toolbar-item>
				<ui-cut-toolbar-item .app="${this.app}"></ui-cut-toolbar-item>
				<ui-paste-toolbar-item .app="${this.app}"></ui-paste-toolbar-item>
				<div class="divider"></div>
				<ui-remove-selected-toolbar-item .app="${this.app}"></ui-remove-selected-toolbar-item>
				<ui-duplicate-toolbar-item .app="${this.app}"></ui-duplicate-toolbar-item>
			</ui-toolbar>
		`
		return html`
			<main>
				<div id="container"></div>
				${ this.app ? toolbars : ``}
			</main>
		`
	}
}
