import {customElement} from 'lit/decorators.js'
import { SaveFileModal } from '../Modal/SaveFileModal'
import { IconName } from './icons'
import { ToolbarItem } from './ToolbarItem'

@customElement('ui-save-file-toolbar-item')
export class SaveFileToolbarItem extends ToolbarItem {
	icon : IconName = 'save'

	disabled = false

	clickHandler () {
		SaveFileModal.open(this.app)
	}
}
