import { Application } from '@eswall/core'
import {html} from 'lit'
import {customElement, query} from 'lit/decorators.js'
import { notesColors } from '../notesColors'
import { Modal } from './Modal'

@customElement('ui-bulk-mode-modal')
export class BulkModeModal extends Modal {
	static open (app: Application) {
		const modal = super.createAndAppend<BulkModeModal>('ui-bulk-mode-modal')
		modal.init(app)
	}

	@query('textarea') textarea !: HTMLTextAreaElement
	title = 'Bulk mode'

	private app!: Application

	init (app: Application) {
		this.app = app
	}

	private save () {
		const content = this.textarea.value
		this.app.createManyNotesOnCenter(content.split('\n'), notesColors[0])

		this.close()
	}

	body () {
		return html`<div>
			<textarea style="width: 100%; height: 300px;"></textarea>
			<ui-button @click="${this.save}">Add</ui-button>
		</div>`
	}
}
