import { SelectedElementsChangedEvent } from '@eswall/core'
import { css, html } from 'lit'
import {customElement, state} from 'lit/decorators.js'
import { IconName } from './icons'
import { notesColors } from '../notesColors'
import { ToolbarItemWithChildrens } from './ToolbarItemWithChildrens'

@customElement('ui-background-toolbar-item')
export class BackgroundToolbarItem extends ToolbarItemWithChildrens {
	static styles = [
		...super.styles,
		css`
			.color-item {
				height: var(--element-size-unit);
				width: var(--element-size-unit);
				border-radius: 50%;
				cursor: pointer;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
			}

			.color-item:hover {
				background: var(--background-secondary);
			}
			.color {
				height: calc(var(--spacing-unit) * 3);
				width: calc(var(--spacing-unit) * 3);
				background: red;
				border-radius: 50%;
				border: 2px solid #2a2b2b;
				cursor: pointer;
			}
			.color-item:hover .color {
				border-color: #025E73;
			}
		`
	]
	icon : IconName = 'color'

	@state() disabled = true

	firstUpdated () {
		this.app.subscribe(SelectedElementsChangedEvent, this.selectedElementsChangedHandler.bind(this))
	}

	private selectedElementsChangedHandler () {
		this.disabled = this.app.getSelectedElementsIds().length === 0
	}

	// eslint-disable-next-line @typescript-eslint/no-empty-function
	clickHandler(): void {}

	subitems () {
		return html`
			${notesColors.map(color => html`
				<div
					class="color-item"
					@click="${this.colorClickHandler(color)}"
				>
					<div
						class="color"
						style="background: ${color};"
					></div>
				</div>
			`)}
		`
	}

	private colorClickHandler (color: string) {
		return () => {
			this.closeSubitems()
			this.app.changeColorForSelectedNotes(color)
		}
	}
}
