import { ClipboardElementsChanged } from '@eswall/core/src/core/Clipboard'
import {customElement, state} from 'lit/decorators.js'
import { IconName } from './icons'
import { ToolbarItem } from './ToolbarItem'

@customElement('ui-paste-toolbar-item')
export class PasteToolbarItem extends ToolbarItem {
	icon : IconName = 'paste'

	@state() disabled = true

	firstUpdated () {
		this.app.subscribe(ClipboardElementsChanged, this.clipboardElementsChangedHandler.bind(this))
	}

	private clipboardElementsChangedHandler () {
		this.disabled = !this.app.isPasteAvailable()
	}

	clickHandler () {
		this.app.paste()
	}
}
