import { SelectedElementsChangedEvent } from '@eswall/core'
import {customElement, state} from 'lit/decorators.js'
import { IconName } from './icons'
import { ToolbarItem } from './ToolbarItem'

@customElement('ui-remove-selected-toolbar-item')
export class RemoveSelectedToolbarItem extends ToolbarItem {
	icon : IconName = 'remove'

	@state() disabled = true

	firstUpdated () {
		this.app.subscribe(SelectedElementsChangedEvent, this.selectedElementsChangedHandler.bind(this))
	}

	private selectedElementsChangedHandler () {
		this.disabled = this.app.getSelectedElementsIds().length === 0
	}

	clickHandler () {
		this.app.removeSelected()
	}
}
