import { Application } from '@eswall/core'
import {html} from 'lit'
import {customElement, query} from 'lit/decorators.js'
import { FileInput } from '../Input'
import { Modal } from './Modal'

@customElement('ui-import-file-modal')
export class ImportFileModal extends Modal {
	static open (app: Application) {
		const modal = super.createAndAppend<ImportFileModal>('ui-import-file-modal')
		modal.init(app)
	}

	@query('ui-file-input') input !: FileInput
	title = 'Import file'

	private app!: Application

	init (app: Application) {
		this.app = app
	}

	private import () {
		const file = this.input.getFile()
		if (!file ) return

		const reader = new FileReader()
		reader.addEventListener('load', (event) => {
			const arrayBuffer = event.target?.result as ArrayBuffer
			if (!arrayBuffer) return

			this.app.import(arrayBuffer)
			this.close()
		})
		reader.readAsArrayBuffer(file)
	}

	body () {
		return html`<div>
			<ui-file-input label="File"></ui-file-input>
			<ui-button @click="${this.import}">Import</ui-button>
		</div>`
	}
}
