import { Application } from '@eswall/core'
import {LitElement, css, html, TemplateResult} from 'lit'
import {property, state} from 'lit/decorators.js'
import {classMap} from 'lit/directives/class-map.js'
import {IconName, icons} from './icons'

export abstract class ToolbarItemWithChildrens extends LitElement {
	static styles = [css`
		.wrapper {
			position: relative;
		}
		.item {
			height: var(--element-size-unit);
			width: var(--element-size-unit);
			margin: var(--spacing-unit);
			border-radius: 50%;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.item:hover {
			background: var(--background-secondary);
		}
		.item:hover svg {
			fill: var(--secondary-color);
		}
		.item svg {
			height: calc(var(--spacing-unit) * 3);
			width: calc(var(--spacing-unit) * 3);
			fill: var(--primary-color);
		}
		.item.disabled svg {
			fill: var(--disabled);
		}
		.item.disabled:hover svg {
			fill: var(--disabled);
		}
		.subitems {
			display: none;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			left: calc(100% + 10px);
			box-sizing: border-box;
			width: fit-content;
			box-shadow: var(--shadow);
			border-radius: var(--border);
			padding: var(--spacing-unit);
			align-items: center;
			z-index: 5;
			background-color: var(--background);
			flex-direction: column;
		}
		.subitems.open {
			display: flex;
		}
	`]

	abstract icon : IconName
	abstract clickHandler () : void
	abstract subitems () : TemplateResult

	@property({attribute: false}) app !: Application

	@state() disabled = false
	@state() isOpen = false

	timeout ?: number

	private mouseoutHandler () {
		this.timeout = window.setTimeout(this.closeSubitems.bind(this), 500)
	}

	private mouseoverHandler () {
		if (this.timeout) clearTimeout(this.timeout)
		this.openSubitems()
	}

	protected closeSubitems () {
		this.isOpen = false
	}

	private openSubitems () {
		if (this.disabled) return false
		this.isOpen = true
	}

	render() {
		return html`
			<div
				class="wrapper"
				@mouseout="${this.mouseoutHandler}"
				@mouseover="${this.mouseoverHandler}"
			>
				<div
					class="item ${classMap({ disabled: this.disabled })}"
					@click="${this.clickHandler}"
				>
					${icons[this.icon]()}
				</div>
				<div class="subitems ${classMap({ open: this.isOpen })}">
					${this.subitems()}
				</div>
			</div>
		`
	}
}
