import { PlatformAdapter } from "../adapters"
import { AddElementsCommand, ChangeElementsPropertiesCommand, Command, RemoveElementsCommand } from "../commands"
import { Note, NoteProperties, WallElement } from "../elements"
import { PublisherEvent, PublisherEventCallback } from "../shared/ObserverPattern"
import { WallElementId } from "../value_objects"
import { Canvas, CanvasBuilder } from "./Canvas"
import { CommandHistory, CommandHistoryEvents } from "./CommandHistory"
import { Wall, WallAnyChangeEvent, WallEvents } from "./Wall"
import { Clipboard, ClipboardEvents } from "./Clipboard"
import { seedElementOnSquareOnCenterOfPosition } from "../utils/WallElementsUtils"

export class Application {
	canvas: Canvas
	constructor(
        private wall: Wall = new Wall([]),
        private commandHistory: CommandHistory = new CommandHistory(),
		private platformAdapter: PlatformAdapter,
		private clipboard: Clipboard,
		canvasBuilder: CanvasBuilder
	) {
		this.canvas = canvasBuilder(this)
		this.wall.subscribe(WallAnyChangeEvent, this.saveDraft.bind(this))
		this.loadDraft()
	}

	executeCommand(command: Command) {
		this.commandHistory.executeCommand(this.wall, command)
	}

	undo() {
		this.commandHistory.undo(this.wall)
	}

	redo() {
		this.commandHistory.redo(this.wall)
	}

	isUndoAvailable () {
		return this.commandHistory.isUndoAvailable()
	}

	isRedoAvailable () {
		return this.commandHistory.isRedoAvailable()
	}

	subscribe <T extends PublisherEvent> (Event : PublisherEvent, callback : PublisherEventCallback<T>) {
		if (WallEvents.includes(Event)) this.wall.subscribe(Event, callback)
		if (CommandHistoryEvents.includes(Event)) this.commandHistory.subscribe(Event, callback)
		if (ClipboardEvents.includes(Event)) this.clipboard.subscribe(Event, callback)
	}

	unsubscribe <T extends PublisherEvent> (Event : PublisherEvent, callback : PublisherEventCallback<T>) {
		if (WallEvents.includes(Event)) this.wall.unsubscribe(Event, callback)
		if (CommandHistoryEvents.includes(Event)) this.commandHistory.unsubscribe(Event, callback)
		if (ClipboardEvents.includes(Event)) this.clipboard.unsubscribe(Event, callback)
	}

	save (name: string) {
		const data = this.wall.exportElements()
		this.platformAdapter.saveToFile(name, this.canvas.toPNGDataURL(), data)
	}

	async export () {
		const data = this.wall.exportElements()
		const array = await this.canvas.toUint8Array()
		return this.platformAdapter.getFile(array, data)
	}

	import(file: ArrayBuffer) {
		const data = this.platformAdapter.getData(file)
		const elements = this.wall.deserializeElements(data)

		const command = new AddElementsCommand(elements)
		this.executeCommand(command)

		this.wall.selectElements(elements)
	}

	async createWallElementOnCanvasClickedPoint (element: WallElement) {
		const position = await this.canvas.turnOnAddElementOnClickMode()

		const properties = element.getProperties()
		properties.position = position
		element.changeProperties(properties)

		const command = new AddElementsCommand([ element ])
		this.executeCommand(command)

		this.canvas.activeElementWithId(element.id)
	}

	createManyNotesOnCenter (contents: string[], color: string) {
		const notes = contents.map(content => {
			const id = WallElementId.generate()
			const note = new Note(id, {
				content,
				background: color,
				position: {
					x: 0,
					y: 0
				}
			})
			return note
		})

		const center = this.canvas.getCanvasCenterPosition()
		seedElementOnSquareOnCenterOfPosition(notes, center, 20)

		const command = new AddElementsCommand(notes)
		this.executeCommand(command)

		this.wall.selectElements(notes)
	}

	addSelectedElement (element: WallElement) {
		this.wall.addSelectedElement(element)
	}

	selectAll () {
		this.wall.selectAll()
	}

	selectElementsByIds(ids: WallElementId[]) {
		this.wall.selectByIds(ids)
	}

	getSelectedElementsIds () {
		return this.wall.getSelectedElementsIds()
	}

	removeSelected() {
		const ids = this.getSelectedElementsIds()
		if (ids.length === 0) return
		const command = new RemoveElementsCommand(ids)
		this.executeCommand(command)
	}

	duplicateSelectedElements(): void {
		const elements = this.wall.selectedElements

		const duplicatedElements = elements
			.map((element: WallElement) => element.duplicate())

		duplicatedElements.forEach((element: WallElement) => {
			element.move(20, 20)
		})


		const command = new AddElementsCommand(duplicatedElements)
		this.executeCommand(command)

		this.wall.selectElements(duplicatedElements)
	}

	copy () {
		this.clipboard.copySelectedElements(this.wall)
	}

	paste () {
		this.clipboard.paste(this, this.wall)
	}

	pasteOnCursorPosition () {
		const position = this.canvas.getCursorPosition()
		this.clipboard.pasteOnPosition(this, this.wall, position)
	}

	isPasteAvailable () {
		return this.clipboard.isPasteAvailable()
	}

	cut () {
		this.clipboard.cutSelectedElements(this, this.wall)
	}

	loadDraft () {
		const data = this.platformAdapter.loadDraft()
		if (!data) return

		const elements = this.wall.deserializeElements(data)
		this.wall.addElements(elements)
	}

	saveDraft () {
		const data = this.wall.exportElements()
		this.platformAdapter.saveDraft(data)
	}

	changeColorForSelectedNotes(color: string) {
		const elements = this.wall.getSelectedElements()
		const notes = elements.filter(element => element instanceof Note) as Note[]

		const idAndPropertiesArray = notes.map(note => {
			const properties = note.getProperties()
			properties.background = color
			return [note.id, properties]
		}) as Array<[WallElementId, NoteProperties]>

		const command = new ChangeElementsPropertiesCommand(idAndPropertiesArray)

		this.executeCommand(command)
	}
}
