import { Application, CanvasBuilder as ICanvasBuilder } from "@eswall/core"
import { ShortcutManagerBuilder } from "./ShortcutManager"
import { Canvas } from "./Canvas"

export function canvasBuilder (
	container: HTMLDivElement,
	shortcutManagerBuilder: ShortcutManagerBuilder
) : ICanvasBuilder {
	return function (app: Application) {
		const shortcutManager = shortcutManagerBuilder(app)
		return new Canvas(app, container, shortcutManager)
	}
}
