import {LitElement, css, html, TemplateResult} from 'lit'
import {state} from 'lit/decorators.js'

export abstract class Modal extends LitElement {
	static createAndAppend <T extends Modal> (tag: string) : T {
		const modal = document.createElement(tag)
		document.body.appendChild(modal)
		return (modal as T)
	}

	static styles = css`
		.modal-wrapper {
			box-sizing: border-box;
			position: fixed;
			display: flex;
			top: 0;
			height: 0;
			width: 100vw;
			height: 100vh;
			align-items: center;
			justify-content: flex-end
		}
		.modal {
			height: 100%;
			box-sizing: border-box;
			display: flex;
			width: fit-content;
			flex-direction: column;
			padding: calc(var(--spacing-unit) * 3);
			box-shadow: var(--shadow);
			border-radius: var(--border);
			z-index: 5;
			background-color: var(--background);
		}
		.overlay {
			position: absolute;
			top: 0;
			left: 0;
			height: 100%;
			width: 100%;
			backdrop-filter: blur(2px);
		}
		.header {
			display: flex;
			width: 100%;
			justify-content: space-between;
			align-items: center;
			padding-bottom: calc(var(--spacing-unit) * 3);
		}
		.header svg {
			width: 24px;
			height: 24px;
			fill: var(--primary-color);
			cursor: pointer;
		}
		.title {
			font-size: 18px;
		}
	`

	@state() title !: string
	abstract body () : TemplateResult

	close () {
		this.remove()
	}

	render() {
		return html`
			<div class="modal-wrapper">
				<div class="modal">
					<div class="header">
						<div class="title">
							${this.title}
						</div>
						<svg @click="${this.close}" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512"><!--! Font Awesome Pro 6.1.2 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. --><path d="M310.6 361.4c12.5 12.5 12.5 32.75 0 45.25C304.4 412.9 296.2 416 288 416s-16.38-3.125-22.62-9.375L160 301.3L54.63 406.6C48.38 412.9 40.19 416 32 416S15.63 412.9 9.375 406.6c-12.5-12.5-12.5-32.75 0-45.25l105.4-105.4L9.375 150.6c-12.5-12.5-12.5-32.75 0-45.25s32.75-12.5 45.25 0L160 210.8l105.4-105.4c12.5-12.5 32.75-12.5 45.25 0s12.5 32.75 0 45.25l-105.4 105.4L310.6 361.4z"/></svg>
					</div>
					${this.body()}
				</div>
				<div class="overlay" @click="${this.close}"></div>
			</div>
		`
	}
}
