import { Note, NoteProperties, Text, TextProperties, WallElement, WallElementProperties } from "../elements"
import { WallElementId } from "../value_objects"

enum Type {
    NOTE = 'n',
	TEXT = 't'
}

export type SerializedElement = {
	t: Type,
	id: string,
	p: TextProperties | NoteProperties | WallElementProperties
}

export class Serializer {
	serialize (elements: WallElement[]) : SerializedElement[] {
		const serializedElementsObjects = elements.map(this.serializeElement.bind(this))

		return serializedElementsObjects
	}

	deserialize (serializedElementsObjects: Array<SerializedElement>) : WallElement[] {
		const elements = serializedElementsObjects
			.map(this.deserializeElement.bind(this))

		return elements
	}

	private serializeElement (element: WallElement) : SerializedElement {
		if (element instanceof Note) return {
			t: Type.NOTE,
			id: element.id.toString(),
			p: element.getProperties()
		}
		if (element instanceof Text) return {
			t: Type.TEXT,
			id: element.id.toString(),
			p: element.getProperties()
		}
		throw new Error(`Can't serialized element ${element}`)
	}

	private deserializeElement (data: SerializedElement) : WallElement  {
		if (data.t === Type.NOTE) return new Note(new WallElementId(data.id), (data.p as NoteProperties))
		if (data.t === Type.TEXT) return new Text(new WallElementId(data.id), (data.p as TextProperties))

		throw new Error(`Can't deserialized element ${data}`)
	}
}
