import { WallElement } from "../elements"

export type Position = {
	x: number,
	y: number
}

export type Rect = {
	x: number,
	y: number,
	width: number,
	height: number
}

function getGroupCenterPosition (elements: WallElement[]) : Position {
	const rect = getWrapperRect(elements)

	return {
		x: rect.x + (rect.width / 2),
		y: rect.y + (rect.height / 2)
	}
}

function getWrapperRect (elements: WallElement[]) : Rect {
	let xMin : number | null = null
	let yMin : number | null = null
	let xMax : number | null = null
	let yMax : number | null = null

	if (!elements || elements.length === 0) throw new Error('Can not calc wrapper rect')

	elements.forEach(element => {
		const { position } = element.getProperties()
		const size = element.getSize()

		if(!xMin || xMin > position.x) xMin = position.x
		if(!yMin || yMin > position.y) yMin = position.y
		if(!xMax || xMax < position.x + size.width) xMax = position.x + size.width
		if(!yMax || yMax < position.y + size.height) yMax = position.y + size.height
	})

	return {
		x: xMin as unknown as number,
		y: yMin as unknown as number,
		width: xMax as unknown as number - (xMin as unknown as number),
		height: yMax as unknown as number - (yMin as unknown as number)
	}
}

export function centerElementsOnCenterOfPosition(elements: WallElement[], position: Position) {
	const currentCenter = getGroupCenterPosition(elements)

	const diffrence = {
		x: position.x - currentCenter.x,
		y: position.y - currentCenter.y
	}

	elements.map(element => {
		element.move(diffrence.x, diffrence.y)
	})
}

export function seedElementOnSquareOnCenterOfPosition (elements: WallElement[], position: Position, gap: number) {
	const sizeX = Math.floor(Math.sqrt(elements.length))
	// chunk elements array
	const matrix = elements.reduce((all: WallElement[][], one, i) => {
		const ch = Math.floor(i / sizeX)
		all[ch] = [...(all[ch] || []), one]
		return all
	}, [])

	let currentY = 0
	matrix.forEach((array) => {
		let currentX = 0

		array.forEach((element) => {
			const properties = element.getProperties()
			properties.position.x = currentX
			properties.position.y = currentY

			element.changeProperties(properties)

			currentX = properties.position.x + element.getSize().width + gap
		})

		const { y, height } = getWrapperRect(array)
		currentY = y + height + gap
	})

	centerElementsOnCenterOfPosition(elements, position)
}
