import { Application } from '@eswall/core'
import {LitElement, css, html} from 'lit'
import {property, state} from 'lit/decorators.js'
import {classMap} from 'lit/directives/class-map.js'
import {IconName, icons} from './icons'

export abstract class ToolbarItem extends LitElement {
	static styles = css`
		.item {
			height: var(--element-size-unit);
			width: var(--element-size-unit);
			margin: var(--spacing-unit);
			border-radius: 50%;
			cursor: pointer;
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
		}
		.item:hover {
			background: var(--background-secondary);
		}
		.item:hover svg {
			fill: var(--secondary-color);
		}
		.item svg {
			height: calc(var(--spacing-unit) * 3);
			width: calc(var(--spacing-unit) * 3);
			fill: var(--primary-color);
		}
		.item.disabled {
			cursor: auto;
		}
		.item.disabled svg {
			fill: var(--disabled);
		}
		.item.disabled:hover svg {
			fill: var(--disabled);
		}
	`

	@state()
		disabled = false

	abstract icon : IconName
	abstract clickHandler () : void

	@property({attribute: false}) app!: Application

	render() {
		return html`
			<div class="item ${classMap({ disabled: this.disabled })}" @click="${this.clickHandler}">
				${icons[this.icon]()}
			</div>
		`
	}
}
