import { Application, Note, Text, WallElement, WallElementType } from "@eswall/core"
import { Canvas } from "../Canvas"
import { CanvasElement } from "./CanvasElement"
import { CanvasNote } from "./CanvasNote"
import { CanvasText } from "./CanvasText"

export class CanvasElementFactory {
	constructor (
        private app : Application,
        private canvas : Canvas
	) { }

	build (element: WallElement) : CanvasElement<WallElementType> {
		if (element instanceof Note) return this.buildNote(element)
		if (element instanceof Text) return this.buildText(element)
		throw new Error('Builder can not build CanvasElement')
	}

	private buildNote(note: Note) {
		return new CanvasNote(this.app, this.canvas, note)
	}

	private buildText(text: Text) {
		return new CanvasText(this.app, this.canvas, text)
	}
}
