import { PlatformAdapter as IPlatformAdapter, SerializedElement } from "@eswall/core"
import { addMetadataFromBase64DataURI, getMetadata, addMetadata } from 'meta-png'

function toBinary(string: string) {
	const codeUnits = new Uint16Array(string.length)
	for (let i = 0; i < codeUnits.length; i++) {
		codeUnits[i] = string.charCodeAt(i)
	}
	const codesAsUint8 = new Uint8Array(codeUnits.buffer)
	let asString = ''

	codesAsUint8.forEach(charCode => {
		asString += String.fromCharCode(charCode)
	})

	return window.btoa(asString)
}

function fromBinary(encoded: string) {
	const binary = window.atob(encoded)
	const bytes = new Uint8Array(binary.length)
	for (let i = 0; i < bytes.length; i++) {
		bytes[i] = binary.charCodeAt(i)
	}
	const codesAsUint16 = new Uint16Array(bytes.buffer)

	let string = ''
	codesAsUint16.forEach(charCode => {
		string += String.fromCharCode(charCode)
	})

	return string
}

// function from https://stackoverflow.com/a/15832662/512042
function downloadURI(uri: string, name: string) {
	const link = document.createElement('a')
	link.download = name
	link.href = uri
	document.body.appendChild(link)
	link.click()
	document.body.removeChild(link)
}

export class PlatformAdapter implements IPlatformAdapter {
	getFile(array: Uint8Array, data: SerializedElement[]): Uint8Array {
		const json = JSON.stringify(data)
		const dataBinary = toBinary(json)

		const pngDataURLWithMetadata = addMetadata(array, 'Data', dataBinary)
		const pngDataURLWithMetadataAndVersion = addMetadata(pngDataURLWithMetadata, 'Version', '1')
		return pngDataURLWithMetadataAndVersion
	}
	saveDraft(data: SerializedElement[]): void {
		const json = JSON.stringify(data)
		window.localStorage.setItem('es_draft', json)
	}

	loadDraft(): SerializedElement[] | null {
		const json = window.localStorage.getItem('es_draft')
		if (!json) return null

		const data = JSON.parse(json)
		return data as SerializedElement[]
	}

	saveToFile (name: string, pngDataURL: string, data: SerializedElement[]) {
		const json = JSON.stringify(data)
		const dataBinary = toBinary(json)

		const pngDataURLWithMetadata = addMetadataFromBase64DataURI(pngDataURL, 'Data', dataBinary)
		const pngDataURLWithMetadataAndVersion = addMetadataFromBase64DataURI(pngDataURLWithMetadata, 'Version', '1')

		downloadURI(pngDataURLWithMetadataAndVersion, name)
	}

	getData(file: ArrayBuffer): SerializedElement[] {
		const fileUint8Array = new Uint8Array(file)
		const version = getMetadata(fileUint8Array, 'Version')

		if (version === '1') return this.getDataV1(fileUint8Array)
		throw new Error(`Can't import file with version ${version}`)
	}

	private getDataV1 (file: Uint8Array): SerializedElement[] {
		const dataBinary = getMetadata(file, 'Data')
		if (!dataBinary) throw new Error(`Can't import file without data`)

		const json = fromBinary(dataBinary)
		const data = JSON.parse(json)
		return data as SerializedElement[]
	}
}
