import { WallElement, WallElementProperties } from "../elements"
import { Publisher, PublisherEvent } from "../shared/ObserverPattern"
import { WallElementId } from "../value_objects"
import { SerializedElement, Serializer } from "./Serializer"

export class ElementEvent extends PublisherEvent {
	constructor (
        public element: WallElement
	) { super() }
}

export class NewElementEvent extends ElementEvent {}
export class RemoveElementEvent extends ElementEvent {}
export class ChangeElementPropertiesEvent extends ElementEvent {}

export class SelectedElementsChangedEvent extends PublisherEvent {}
export class WallAnyChangeEvent extends PublisherEvent {}

export type WallEventsType =
	| NewElementEvent
	| RemoveElementEvent
	| ChangeElementPropertiesEvent
	| SelectedElementsChangedEvent
	| WallAnyChangeEvent

export const WallEvents : WallEventsType[] = [NewElementEvent, RemoveElementEvent, ChangeElementPropertiesEvent, SelectedElementsChangedEvent, WallAnyChangeEvent]

export class Wall extends Publisher {
	selectedElements : WallElement[] = []
	constructor(
        private elements: WallElement[],
		private serializer : Serializer = new Serializer()
	) {
		super()
	}

	addElements(elements: WallElement[]) {
		elements.forEach(element => this.addElement(element))
	}

	addElement(element: WallElement) {
		this.elements.push(element)
		this.notify(new NewElementEvent(element))
	}

	removeElement(id: WallElementId) : WallElement {
		const index = this.elements.findIndex(element => element.idEqual(id))
		const element = this.elements.splice(index, 1)[0]
		this.notify(new RemoveElementEvent(element))
		this.deselectElementById(element.id)
		return element
	}

	findElementById(id: WallElementId): WallElement | null {
		return this.elements.find(element => element.idEqual(id)) || null
	}

	findElementsByIds(ids: WallElementId[]): WallElement[]  {
		return this.elements.filter(element => ids.some(id => element.idEqual(id)))
	}

	changeElementProperties (id: WallElementId, properties: WallElementProperties) {
		const element = this.findElementById(id)
		if (!element) throw new Error('Element not founded')
		const oldProperties = element.changeProperties(properties)
		this.notify(new ChangeElementPropertiesEvent(element))
		return oldProperties
	}

	exportElements() : SerializedElement[] {
		return this.serializer.serialize(this.elements)
	}

	addSelectedElement (element: WallElement) {
		this.selectedElements.push(element)
		this.notify(new SelectedElementsChangedEvent())
	}

	deserializeElements(data: SerializedElement[]) {
		const elements = this.serializer.deserialize(data)
		return elements
	}

	selectByIds (ids: WallElementId[]) {
		const elements = this.findElementsByIds(ids)
		this.selectElements(elements)
	}

	selectElements (elements: WallElement[]) {
		this.selectedElements = elements

		this.notify(new SelectedElementsChangedEvent())
	}

	private deselectElementById (id: WallElementId) {
		this.selectedElements = this.selectedElements.filter(element => !element.idEqual(id))
		this.notify(new SelectedElementsChangedEvent())
	}

	getSelectedElementsIds() {
		return this.selectedElements.map(element => element.id)
	}

	getSelectedElements() {
		return this.selectedElements
	}

	selectAll() {
		this.selectElements(this.elements)
	}

	protected notify (event: PublisherEvent) {
		super.notify(new WallAnyChangeEvent())
		super.notify(event)
	}
}
