import { Text, WallElementId } from '@eswall/core'
import {customElement} from 'lit/decorators.js'
import { IconName } from './icons'
import { ToolbarItem } from './ToolbarItem'

@customElement('ui-new-text-toolbar-item')
export class NewTextToolbarItem extends ToolbarItem {
	icon : IconName = 'text'

	clickHandler () {
		const id = WallElementId.generate()
		const text = new Text(id, {
			content: 'Text',
			position: {
				x: 0, y: 0
			},
			size: {
				width: 400,
				height: 32
			}
		})
		this.app.createWallElementOnCanvasClickedPoint(text)
	}
}
