import { BeforeDuplicateEvent, ChangeElementsPropertiesCommand, Text } from "@eswall/core"
import Konva from "konva"
import { CanvasElement } from "./CanvasElement"
import { ContentEditableArea } from "./ContentEditableArea"
import { FontSizeCalculator } from "../utils/FontSizeCalculator"
import { KonvaEventObject } from "konva/lib/Node"

export class CanvasText extends CanvasElement<Text> {
	private textNode !: Konva.Text
	private contentEditableArea ?: ContentEditableArea

	createKonvaElement(): void {
		this.draggable(true)

		this.textNode = new Konva.Text({
			align: 'left',
			verticalAlign: 'middle',
			fontFamily: 'Nunito',
		})

		this.setProperties()

		this.add(this.textNode)
		this.canvas.addToLayer(this)

		this.on('click', this.clickHandler.bind(this))
		this.on('dragend', this.dragEndHandler.bind(this))

		this.element.subscribe(BeforeDuplicateEvent, this.beforeDuplicateHandler.bind(this))
	}

	propertiesChangedHandler() {
		this.setProperties()
	}

	click () {
		this.clickHandler()
	}

	private clickHandler (event ?: KonvaEventObject<MouseEvent>) {
		if (event && event.evt.shiftKey) {
			this.app.addSelectedElement(this.element)
		}
		else {
			this.app.selectElementsByIds([this.element.id])
			setTimeout(() => {
				this.addContentEditableArea()
			})
		}
	}

	private beforeDuplicateHandler () {
		this.saveDataFromContentEditableArea()
	}

	private saveDataFromContentEditableArea() {
		if (!this.contentEditableArea) return
		return this.contentEditableArea.forceContentChangedEvent()
	}

	private setProperties() {
		const properties = this.element.getProperties()
		this.textNode.text(properties.content)
		this.textNode.width(properties.size.width)
		this.textNode.height(properties.size.height)
		this.textNode.fontSize(new FontSizeCalculator(this.textNode).getMaxFontSize())
		this.x(properties.position.x)
		this.y(properties.position.y)
	}

	private dragEndHandler () {
		const newProperties = this.element.getProperties()
		newProperties.position.x = this.x()
		newProperties.position.y = this.y()

		this.app.executeCommand(new ChangeElementsPropertiesCommand([
			[this.element.id, newProperties]
		]))
	}

	private addContentEditableArea() {
		this.textNode.hide()

		this.contentEditableArea = ContentEditableArea.create(this.canvas, this.app, this.textNode)

		this.contentEditableArea.addEventListener('beforeRemove', this.contentEditableAreaRemovedHandler.bind(this))
		this.contentEditableArea.addEventListener('contentChanged', this.contentEditableAreaContentChangedHandler.bind(this))
	}

	private contentEditableAreaContentChangedHandler(event: Event) {
		if ((event as CustomEvent).detail.content === this.textNode.text()) return

		const newProperties = this.element.getProperties()
		newProperties.content = (event as CustomEvent).detail.content

		this.app.executeCommand(new ChangeElementsPropertiesCommand([
			[this.element.id, newProperties]
		]))
	}

	private contentEditableAreaRemovedHandler() {
		this.textNode.show()
	}
}

