import { ShortcutManager as IShortcutManager } from "@eswall/canvas"
import { Application } from "@eswall/core"
import hotkey, { KeyHandler } from 'hotkeys-js'

export class ShortcutManager implements IShortcutManager {
	contentEditShortcutsAreEnable = true

	constructor (private app: Application) {
		this.initShortcuts()
	}

	initShortcuts() {
		const iCESAE = this.ifContentEditShortcutsAreEnable.bind(this)

		hotkey('Ctrl+Z', iCESAE(() => this.app.undo()))
		hotkey('Ctrl+Y', iCESAE(() => this.app.redo()))
		hotkey('Ctrl+A', iCESAE(() => this.app.selectAll()))
		hotkey('Ctrl+C', iCESAE(() => this.app.copy()))
		hotkey('Ctrl+X', iCESAE(() => this.app.cut()))
		hotkey('Ctrl+V', iCESAE(() => this.app.pasteOnCursorPosition()))
		hotkey('Del', iCESAE(() => this.app.removeSelected()))
		hotkey('Ctrl+D', this.prevent(() => this.app.duplicateSelectedElements()))
	}

	prevent (callback: () => void): KeyHandler {
		return (event: KeyboardEvent) => {
			event.preventDefault()
			callback()
		}
	}

	ifContentEditShortcutsAreEnable(callback: () => void): KeyHandler {
		return () => {
			if (!this.contentEditShortcutsAreEnable) return
			callback()
		}
	}

	disableContentEditShortcuts(): void {
		this.contentEditShortcutsAreEnable = false
	}
	enableContentEditShortcuts(): void {
		this.contentEditShortcutsAreEnable = true
	}
}
