import { Publisher, PublisherEvent } from "../shared/ObserverPattern"
import { WallElementId } from "../value_objects"

export type WallElementProperties = {
    position: {
        x: number,
        y: number,
    }
}

export type WallElementSize = {
	width: number,
	height: number
}

export class PropertiesChangedEvent extends PublisherEvent {}
export class BeforeDuplicateEvent extends PublisherEvent {}

export abstract class WallElement <Properties extends WallElementProperties = WallElementProperties> extends Publisher {
	constructor(
        public id: WallElementId,
        protected properties: Properties
	) { super() }

	idEqual(id: WallElementId): boolean {
		return this.id.equal(id)
	}

	getProperties(): Properties {
		return JSON.parse(JSON.stringify(this.properties))
	}

	changeProperties(newProperties: Properties): Properties {
		const oldProperties = this.properties
		this.properties = newProperties
		this.notify(PropertiesChangedEvent)
		return oldProperties
	}

	duplicate() {
		this.notify(BeforeDuplicateEvent)
		// eslint-disable-next-line @typescript-eslint/no-explicit-any
		const WallElement = this.constructor as any
		return new WallElement(WallElementId.generate(), this.getProperties())
	}

	move(x: number, y: number) {
		const newProperties = this.getProperties()
		newProperties.position.x = newProperties.position.x + x
		newProperties.position.y = newProperties.position.y + y

		this.changeProperties(newProperties)
	}

	abstract getSize () : WallElementSize
}
